<template>
  <span ref="btn">
    <v-btn flat large>
      <slot></slot>
    </v-btn>
  </span>
</template>

<script>
  const COMPONENT_NAME = 'uploader-btn'

  export default {
    name: COMPONENT_NAME,
    props: {
      directory: {
        type: Boolean,
        default: false
      },
      single: {
        type: Boolean,
        default: false
      },
      attrs: {
        type: Object,
      }
    },
    mounted () {
      this.$nextTick(() => {
        this.$store.state.uploader.assignBrowse(this.$refs.btn, this.directory, this.single, this.attrs)
      })

    }
  }
</script>

<style>
  uploader-btn {
    display: inline-block;
    position: relative;
    padding: 4px 8px;
    font-size: 100%;
    line-height: 1.4;
    color: #666;
    border: 1px solid #666;
    cursor: pointer;
    border-radius: 2px;
    background: none;
    outline: none;
  }
  .uploader-btn:hover {
    background-color: rgba(0, 0, 0, .08);
  }
</style>



