<template>
  <v-app d="app">
    <v-toolbar lg1 dark color="primary">
      <v-flex lg8 offset-lg2>
        <v-toolbar flat color="transparent">
            <v-toolbar-items>
              <v-btn :to="navbtn_to" depressed large color="primary">
                <v-icon ref="btnBack"  dark large>arrow_back</v-icon>
                <v-icon ref="btnResults" large>list</v-icon>
              </v-btn>
            </v-toolbar-items>
            <v-spacer></v-spacer>
            <v-toolbar-title>{{title}}</v-toolbar-title>
            <v-spacer></v-spacer>

            <v-toolbar-items >
              <v-btn depressed  large @click="refresh()" color="primary">
                <v-icon large>refresh</v-icon>
              </v-btn>
            </v-toolbar-items>
        </v-toolbar>
      </v-flex>
    </v-toolbar>

    <router-view :app="this"></router-view>
  </v-app>
</template>

<script>
  import Uploader from '../node_modules/simple-uploader.js'

export default {
data(){
  return{
    title: "",
    navbtn_to: "/results",

    options: {
      target: this.$http.options.root+'/upload',
      testChunks: false
    },
  }
},
  methods:{
    refresh(){
      location.reload();
    },
  },

  mounted(){
    this.$refs.btnBack.style.display = 'none';
    this.$refs.btnResults.style.display = '';
    this.navbtn_to = '/results';
  },

  created(){
    this.$store.state.uploader = new Uploader(this.options)
  },
}
</script>

<style>
#app {
  background-color: #FFFDE7;
  height: 100%;
  width: 100%;
  text-align: center;
}
</style>



