<template>
  <v-layout row>
    <v-flex>
      <v-card>
        <v-list two-line>
          <span v-if="fileList.length === 0">No files yet.</span>
            <template v-for="(file, index) in fileList">
              <uploader-file :file="file" :list="true"></uploader-file>
              <v-divider v-if="index + 1 < fileList.length" :key="index"></v-divider>
            </template>
        </v-list>
      </v-card>
    </v-flex>
  </v-layout>
</template>

<script>
  import UploaderFile from './file.vue'

  const COMPONENT_NAME = 'uploader-list'

  export default {
    name: COMPONENT_NAME,
    props:{
      fileList: Array,
    },
    components: {
      UploaderFile
    }
  }
</script>

<style>
</style>



